<template>
  <v-container fluid fill-height style="padding:0;" id="generator" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="Generator">Generator原理</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-layout row wrap>
                        <v-flex xs12 sm6 pr-1>
                            <div class="part">
                                <p>理解如下几点，生成器函数就ok了</p>
                                <p>1. <code>Generator</code> 本质上是生成一个可遍历函数</p>
                                <p>2. <code>yield</code> 语句的返回值是<code>next</code>函数传递的参数</p>
                                <p>3. <code>yield</code> 后面的跟着的值是<code>next</code>函数的返回值</p>
                                <p>4. <code>yield</code> 语句在初次执行时会有一个初始化的过程，此过程不接受<code>next</code>传递的值</p>
                                <p>5. <code>next</code> 函数传递的值，能够在上一次<code>yield</code>语句的返回值中取到</p>
                                <p>6. <code>f.return(f.next().value);</code> 这种写法能够只执行<code>Generator</code>的部分片段</p>
                            </div>
                        </v-flex>
                        <v-flex xs12 sm6>
                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                <v-tab-item key="js">
                                    <v-card flat>
                                        <v-card-text>
                                            <pre class="code">{{pres[0]}}</pre>
                                        </v-card-text>
                                    </v-card>
                                </v-tab-item>
                            </v-tabs>
                        </v-flex>
                    </v-layout>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex xs12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="200"
              >
                <v-card-title>
                  <h3 id="demo">Generator实战</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <v-layout row wrap>
                        <v-flex xs12 sm6 pr-1>
                            <div class="part">
                                <p>1. <code>Generator</code> 函数可以用来做流程控制，可以让一段复杂的逻辑流程化</p>
                                <p>2. demo要实现的功能：传递任意多个数组，将每一个数组的最大值和所有数组的最大值拼接成一个数组输出</p>
                            </div>
                        </v-flex>
                        <v-flex xs12 sm6>
                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                <v-tab-item key="js">
                                    <v-card flat>
                                        <v-card-text>
                                            <pre class="code">{{pres[1]}}</pre>
                                        </v-card-text>
                                    </v-card>
                                </v-tab-item>
                            </v-tabs>
                        </v-flex>
                    </v-layout>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

<script>
import Blog from "../Blog.vue";
import { setInterval } from "timers";
export default {
  components: { Blog },
  data() {
    return {
      time: "2018-12-13",
      title: "使用Generator编写优雅的代码",
      items: [
        {
          title: "Generator原理",
          target: "#Generator"
        },
        { divider: true },
        {
          title: "Generator实战",
          target: "#demo"
        },
      ],
      pres: 
[
    `
    function *fn() {
        let i = Math.random();
        let p = yield i;

        let j = Math.random() * p;
        yield j;
    }
    const f = fn();
    f.next(); // {value: 0.48682324805468724, done: false}
    f.next(1000); // {value: 374.5937454669304, done: false}
    `,
    `
    function findMax() {
        const params = Array.from(arguments); // 获取所有数组
        const length = params.length;
        let tmpMaxs = [];
        let i = 0;
        function *fn() {
            yield '初始化'; // yield会有一个初始化的过程
            while(i < length) {
                const tmp = Math.max.apply(null, params[i]);
                tmpMaxs.push(tmp);
                yield tmp; // 获取每一个数组的最大值
                i++;
            }
            const max = Math.max.apply(null, tmpMaxs); // 获取所有数组的最大值
            tmpMaxs.push(max);
            yield max;
        }

        const f = fn();
        let res = f.next(); // 初始化yield
        while(!res.done) { // 执行每一个yield获取最大值
            res = f.next(); 
        }
        return tmpMaxs;
    }
    let max = findMax([10,59,1,2,3], [5,8,9,30,21], [1,20,3,0,3]);
    console.log(max); // 输出 [59, 30, 20, 59]
    `
]
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

<style lang="scss">
#generator {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }

    .part{
        background: #f9f9f5;
        width: 100%;
        height: 100%;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }
}
</style>

